body {
  background-color: #c3e2e8a0;
  background-image: url("1672248930_kalix-club-p-pattern-kotiki-instagram-47.9767f18a.jpg");
  background-repeat: repeat;
  width: 100%;
  height: 100%;
  margin: 0;
}

.loader {
  color: #3b2d5a;
  text-align: center;
  align-items: center;
  min-width: 350px;
  height: 50px;
  margin-top: 50px;
  margin-left: auto;
  margin-right: auto;
  font-size: 28px;
  font-style: normal;
  font-weight: 700;
  display: block;
}

.loader:before {
  content: "";
  color: #3b2d5a;
  box-sizing: border-box;
  border-radius: 50%;
  width: 15px;
  height: 15px;
  margin: 15px auto;
  animation: 1s linear infinite alternate animloader;
  display: block;
}

@keyframes animloader {
  0% {
    box-shadow: -38px -12px, -14px 0, 14px 0, 38px 0;
  }

  33% {
    box-shadow: -38px 0, -14px -12px, 14px 0, 38px 0;
  }

  66% {
    box-shadow: -38px 0, -14px 0, 14px -12px, 38px 0;
  }

  100% {
    box-shadow: -38px 0, -14px 0, 14px 0, 38px -12px;
  }
}

.error, .error:before {
  color: #f70f12;
}

@media screen and (min-width: 650px) {
  .loader {
    font-size: 32px;
  }
}

.breed-select {
  color: #1c0c42;
  border: 3px solid #9068ee5c;
  border-radius: 5px;
  outline-color: #0000;
  width: 100%;
  height: 50px;
  margin-top: 10px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
  display: flex;
  position: relative;
  box-shadow: 0 0 8px #000;
}

@media screen and (min-width: 650px) {
  .breed-select {
    max-width: 800px;
    height: 60px;
    font-size: 28px;
  }
}

@media screen and (min-width: 950px) {
  .breed-select {
    font-size: 32px;
  }
}

.cat-info {
  background-color: #fff;
  background-position: 50%;
  border-radius: 10px;
  flex-direction: column;
  justify-content: flex-start;
  align-items: baseline;
  min-width: 320px;
  margin-top: 5px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  box-shadow: 0 3px 5px 2px #00000094;
}

.picture {
  object-fit: cover;
  background-repeat: no-repeat;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

